.navbar {
    color: white;
    background-color: #242526;
    border-bottom: white solid 1px;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    position: sticky;
    top: 0;
  }

  .navbar-name {

  }

  .nav-drawer-container {
    display: none;
  }

  .logo {
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: 20px;
    font-weight: bold;
    color: #61dafb;
    text-decoration: none;
  }

  .link {
    color: inherit;
    text-decoration: none;
  }

  .list {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
  }
  
  .listItem {
    list-style: none;
    margin-right: 1.5rem;
    cursor: pointer;
  }
  
  .avatar {
    height: 40px;
    width: 40px;
    border-radius: 50%;
    object-fit: cover;
  }

  @media (max-width: 768px) {
    #nav-logout {
      display: none;
    }

    .navbar-name {
      font-size: 1rem;
    }

    .listItem {
      list-style: none;
      margin-right: 0.5rem;
      cursor: pointer;
    }

    .nav-drawer-container {
      display: block;
    }
}

